<div class="hyContent"><div class="tech"><div class="tech__wrapper"><div class="tech__title">TECHNOLOGIES & HARDWARE</div><div class="tech__subTitle">USED BY HYDRA VR.</div><button class="tech__button"></button></div><div class="tech__desktop" v-if="isDesktop"><div class="tech__imgWrapper" v-for="item in imgs" :key="item.id"><img class="tech__img" :src="item.img" :alt="item.alt" loading="lazy"></div></div><div class="tech__mobile" v-else><HyCarousel class="tech__carousel" :items-to-show="1" :wrap-around="true" ref="myCarousel"><HySlide class="tech__slide" v-for="item in imgs" :key="item.id"><div class="tech__imgWrapper"><img class="tech__img" :src="item.img" :alt="item.alt" loading="lazy"></div></HySlide><template class="tech__btns" #addons><div class="tech__btnsWrapper"><button class="tech__prev" @click="myCarousel.prev()"></button><button class="tech__next" @click="myCarousel.next()"></button></div></template></HyCarousel></div></div></div>