<div class="cSlider"><div class="cSlider__desktop" v-if="isDesktop"><div class="cSlider__content" v-for="item in info" :key="item.id"><div class="cSlider__logoWrapper"><img class="cSlider__img" :src="item.img" loading="lazy" alt="alt"></div><div class="cSlider__texts"><h2 class="cSlider__title">{{item.title}}</h2><span class="cSlider__text">{{item.text}}</span></div></div></div><div class="cSlider__mobile" v-else><HyCarousel class="cSlider__carousel" :items-to-show="1" :wrap-around="true" ref="myCarousel"><HySlide class="cSlider__slide" v-for="item in info" :key="item.id"><div class="cSlider__logoWrapper"><img class="cSlider__img" :src="item.img" loading="lazy" alt="alt"></div><div class="cSlider__texts"><h2 class="cSlider__title">{{item.title}}</h2><span class="cSlider__text">{{item.text}}</span></div></HySlide><template class="cSlider__btns" #addons><div class="cSlider__btnsWrapper"><button class="cSlider__prev" @click="myCarousel.prev()"></button><button class="cSlider__next" @click="myCarousel.next()"></button></div></template></HyCarousel></div></div>