<div class="hyContent"><div class="servicesSection"><div class="servicesSection__descWrapper"><HyDescription :title="titleForDescr" :subTitle="subTitleForDescr" :text="textForDescr"></HyDescription></div><div class="servicesSection__desktop" v-if="isDesktop"><div class="servicesSection__carousel"><div class="servicesSection__slide" v-for="item in info" :key="item.id"><div class="servicesSection__imgBg"><div class="servicesSection__imgWrapper"><img class="servicesSection__img" :src="item.img" loading="lazy" alt="alt"></div></div><h3 class="servicesSection__title">{{item.title}}</h3><span class="servicesSection__text">{{item.text}}</span><button class="servicesSection__btn">{{item.btnText}}</button></div></div><div class="servicesSection__vector"><img class="servicesSection__vectorImg" :src="vector" loading="lazy" alt="vector"></div></div><div class="servicesSection__mobile" v-else><HyCarousel class="servicesSection__carousel" :items-to-show="1" :wrap-around="true" ref="myCarousel"><HySlide class="servicesSection__slide" v-for="item in info" :key="item.id"><div class="servicesSection__imgBg"><div class="servicesSection__imgWrapper"><img class="servicesSection__img" :src="item.img" loading="lazy" alt="alt"></div></div><h3 class="servicesSection__title">{{item.title}}</h3><span class="servicesSection__text">{{item.text}}</span><button class="servicesSection__btn">{{item.btnText}}</button></HySlide><template class="servicesSection__btns" #addons><div class="servicesSection__btnsWrapper"><button class="servicesSection__prev" @click="myCarousel.prev()"></button><button class="servicesSection__next" @click="myCarousel.next()"></button></div></template></HyCarousel></div></div></div>