<div class="hyContent"><div class="howTo"><div class="howTo__descWrapper"><HyDescription :title="titleForDescr" :subTitle="subTitleForDescr" :text="textForDescr"></HyDescription></div><div class="howTo__desktop" v-if="isDesktop"><div class="howTo__contentWrapper" v-for="item in info" :key="item.id"><span class="howTo__item">{{item.id}}</span><div class="howTo__titleBlock"><div class="howTos__arrowWrapper"><img class="howTos__img" :src="right" alt="arrow" loading="lazy"></div><div class="howTo__title" v-html="item.title"></div></div></div><div class="howTo__vector"><img class="howTo__vectorImg" :src="howToVector" loading="lazy" alt="vector"></div></div><div class="howTo__mobile" v-else><HyCarousel class="howTo__carousel" :items-to-show="1" :wrap-around="true" ref="myCarousel"><HySlide class="howTo__slide" v-for="item in info" :key="item.id"><div class="howTo__contentWrapper"><span class="howTo__item">{{item.id}}</span><div class="howTo__titleBlock"><div class="howTo__title" v-html="item.title"></div></div></div></HySlide><template class="howTo__btns" #addons><div class="howTo__btnsWrapper"><button class="howTo__prev" @click="myCarousel.prev()"></button><button class="howTo__next" @click="myCarousel.next()"></button></div></template></HyCarousel></div></div></div>