<div class="hyContent"><header class="header"><div class="header__logo"><span class="header__logoWrapper"><img class="header__logoImg" :src="logo" loading="lazy" alt="logo"></span><span class="header__logoWrapper header__logoWrapper--small"><img class="header__logoImg" :src="logoText" loading="lazy" alt="logoText"></span></div><nav class="header__menu"><ul class="header__nav"><li class="header__navItem" v-for="item in navItems" :key="item.id" @click="$scrollTo('.'+item.id)">{{item.text}}</li></ul></nav><div class="header__right"><div class="header__btns"><button class="header__btn" @click="$scrollTo('.contactUs')">CONTACT US</button><button class="header__btnGradient" @click="$scrollTo('.formJoin')">JOIN HYDRA</button></div><div class="header__burger"><span class="header__burgerWrapper"><img class="header__logoImg" :src="burgerLogo" loading="lazy" alt="logo"></span></div></div></header></div>